@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
:root {
  --primary-color: #ff9900;
  --secondary-color: #ffc700;
}
body {
  font-family: "Poppins", sans-serif;
}

.container {
  max-width: 1920px;
  margin: 0 auto;
}

.MuiButton-label {
  color: white;
}

.MuiDialogTitle-root h2 {
  color: white;
}

.MuiButton-containedPrimary {
  background-color: #ff9100 !important;
}

.MuiButton-containedPrimary:hover {
  background-color: #ff9100 !important;
}

.MuiDialogTitle-root {
  background-color: #ff9100 !important;
}

.MuiSvgIcon-root {
  fill: white !important;
}

.MuiPaper-rounded {
  border-radius: 10px !important;
}

.home-page {
  position: static;
  overflow-x: hidden;
  overflow-y: hidden;
  min-height: 767px;
  position: fixed;
}

#home-page-map {
  width: 100vw;
  height: 100vh;
  min-width: 948px;
  overflow: hidden;
}

#orange {
  color: orange;
}

.home-navbar-wrapper {
  position: absolute;
  top: 0px;
  width: 100vw;
  height: 90px;
  z-index: 10;
  position: fixed;
}

.home-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 30px;
  background: #f8f5f1;
  height: 100%;
  border-radius: 0px 0px 20px 20px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.07);
  -webkit-backdrop-filter: blur(40px);
          backdrop-filter: blur(40px);
  min-width: 948px;
}

.home-navbar-navigations {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 40px;
}

.home-navbar-navigations a {
  text-decoration: none;
  font-weight: 400;
  font-size: 1.1em;
  color: black;
}

.home-navbar-navigations a:first-child {
  color: #ff9900;
}

.home-navbar-logo {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 15px;
  align-items: center;
}

.home-navbar-logo img {
  width: 50px;
  -webkit-filter: drop-shadow(0px 4px 20px rgba(255, 153, 0, 0.3));
          filter: drop-shadow(0px 4px 20px rgba(255, 153, 0, 0.3));
}

.home-navbar-logo h2 {
  font-weight: 500;
}

.home-navbar-dash-dropdown {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 10px;
  background-color: #ff9900;
  padding: 10px 35px;
  border-radius: 15px;
  box-shadow: 0px 5px 8px rgb(255 153 0 / 34%);
  cursor: pointer;
  margin-top: 140px;
}

.home-navbar-dash-dropdown button {
  background-color: transparent;
  border: none;
  color: white;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
}

.hone-navbar-dash-dropdown-wrapper {
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.navbar-dropdown-navigation {
  width: 100%;
  height: 140px;
  max-height: 140px;
  background-color: #f8f5f1;
  box-shadow: 0px 7px 15px rgba(0, 0, 0, 0.15);
  -webkit-backdrop-filter: blur(40px);
          backdrop-filter: blur(40px);
  padding: 10px 18px;
  border-radius: 0px 0px 10px 10px;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  text-align: start;
  font-size: 1.1rem;
  font-weight: bolder;
  transition: 400ms;
  /* transform: scaleY(140px); */
  -webkit-transform-origin: top;
          transform-origin: top;
}

.navbar-dropdown-navigation a {
  text-decoration: none;
  color: #707070;
}

.navbar-dropdown-navigation-button {
  display: grid;
  grid-template-columns: 1fr auto;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
}

.navbar-dropdown-navigation-button a {
  font-size: 1em;
  font-weight: 400;
  text-align: start;
  color: black;
  font-weight: 500;
  transition: 200ms;
}

.navbar-dropdown-navigation-button a:hover {
  color: var(--primary-color);
}
.navbar-dropdown-navigation-button img {
  width: 27px;
}

.home-page-bottom-tracker {
  position: fixed;
  bottom: 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  right: 0;
  left: 0;
  background-color: transparent;
  display: grid;
  grid-template-columns: 300px 300px 300px;
  grid-gap: 20px;
  justify-content: center;
  margin: auto;
  margin: 100% auto 0 auto;
  min-width: 948px;
  z-index: 3;
  position: fixed;
  transition: 600ms;
}

.home-page-bottom-tracker--card {
  /* background-color: #f8f5f1; */
  background-color: rgba(255, 255, 255, 0.5);
  margin: 20px;
  border-radius: 20px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  -webkit-backdrop-filter: blur(30px);
          backdrop-filter: blur(30px);
  padding: 13px 20px;
  display: grid;
  grid-template-rows: 1fr 1fr;
}
.home-page-bottom-tracker--card h3 {
  font-size: 1.1em;
  font-weight: 500;
}
.bottom-tracker--card-count {
  display: grid;
  grid-template-columns: 1fr auto;
  justify-content: center;
  align-items: center;
}

.bottom-tracker--card-count h1 {
  font-size: 2.3rem;
  font-weight: 600;
  color: #ff9900;
}

.home-page-map-navigations {
  width: 250px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 30px;
  margin: 100px auto 0 0;
  background-color: transparent;
  z-index: 4;
  display: grid;
  grid-template-rows: 300px 300px;
  justify-content: center;
  align-items: center;
  position: fixed;
  /* background-color: red; */
  transition: 600ms;
}

.home-page-map-navigations--card {
  width: 250px;
  height: 250px;
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  -webkit-backdrop-filter: blur(40px);
          backdrop-filter: blur(40px);
  border-radius: 20px;
  padding: 20px;
}

.home-page-map-navigations--card h3 {
  margin-bottom: 10px;
  font-size: 1.3rem;
  font-weight: 500;
}

.map-index-list {
  display: grid;
  align-items: center;
  justify-content: start;
  grid-template-columns: auto auto;
  grid-gap: 20px;
}

.map-index-list p {
  font-weight: 500;
}

.map-filter-buttons {
  display: grid;
  grid-template-rows: auto auto auto;
  align-items: center;
  grid-gap: 27px;
}

.map-filter-checkbox {
  display: grid;
  grid-template-columns: auto auto;
  justify-content: start;
  grid-gap: 10px;
  position: relative;
}

.map-filter-checkbox input {
  width: 30px;
  height: 30px;
  border-radius: 5px;
  cursor: pointer;
}

.map-filter-checkbox input[type="checkbox"] {
  position: relative;
  background-color: white;
  -webkit-appearance: none;
          appearance: none;
  box-shadow: inset 0px 1px 6px rgba(0, 0, 0, 0.25);
  transition: 0.1s ease-in-out;
  cursor: pointer;
}

.map-filter-checkbox input:checked[type="checkbox"] {
  box-shadow: inset 0px 1px 6px rgba(0, 0, 0, 0.25);
  padding: 10px;
}

.map-filter-checkbox input[type="checkbox"]::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 20px;
  height: 20px;
  background: white;
  border-radius: 4px;
  transition: 0.5s ease-in-out;
}

.map-filter-checkbox input:checked[type="checkbox"]::before {
  background: #ff9900;
}

.geocoder {
  position: absolute;
  z-index: 1;
  width: 50%;
  left: 50%;
  margin-left: -25%;
  top: 10px;
}

.mapboxgl-ctrl-top-right {
  padding-top: 100px !important;
  padding-right: calc(100vw / 2 - 290px / 2);
}

/* Small Screens :-/ */
@media only screen and (max-height: 859px) and (max-width: 1440px) {
  .home-page-bottom-tracker {
    position: fixed !important;
    right: 0;
    left: unset;
    margin: auto;
  }
}

@media only screen and (max-height: 962px) and (max-width: 1207px) {
  .home-page-bottom-tracker {
    min-width: unset;
    grid-template-columns: 210px 210px 210px;
    width: auto;
  }

  .home-page-bottom-tracker--card h3 {
    font-size: 0.9rem;
  }

  .home-page-map-navigations {
    grid-template-rows: 200px 200px;
    grid-gap: 20px;
    margin: 100px auto 0 0px;
    justify-content: start;
  }

  .home-page-map-navigations--card {
    width: 200px;
    height: 200px;
  }

  .map-index-list {
    font-size: 0.8rem;
  }
  .map-index-list img {
    width: 40px;
  }

  .map-filter-checkbox input {
    width: 20px;
    height: 20px;
  }
  .map-filter-checkbox {
    font-size: 0.8rem;
  }

  .map-filter-checkbox input[type="checkbox"]::before {
    width: 13px;
    height: 13px;
  }

  .home-page-map-navigations--card h3 {
    font-size: 1rem;
  }
}

@media only screen and (max-height: 693px) and (max-width: 1366px) {
  .home-page-bottom-tracker {
    min-width: unset;
    grid-template-columns: 210px 210px 210px;
    width: auto;
  }

  .home-page-bottom-tracker--card h3 {
    font-size: 0.9rem;
  }

  .home-page-map-navigations {
    grid-template-rows: 200px 200px;
    grid-gap: 20px;
    margin: 100px auto 0 0px;
    justify-content: start;
  }

  .home-page-map-navigations--card {
    width: 200px;
    height: 200px;
  }

  .map-index-list {
    font-size: 0.8rem;
  }
  .map-index-list img {
    width: 40px;
  }

  .map-filter-checkbox input {
    width: 20px;
    height: 20px;
  }
  .map-filter-checkbox {
    font-size: 0.8rem;
  }

  .map-filter-checkbox input[type="checkbox"]::before {
    width: 13px;
    height: 13px;
  }

  .home-page-map-navigations--card h3 {
    font-size: 1rem;
  }
}

/* IPad */
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
  .home-navbar {
    min-width: unset;
  }

  .home-navbar-logo h2 {
    font-size: 1rem;
  }

  .home-navbar-dash-dropdown {
    padding: 10px 10px;
  }

  .home-navbar-dash-dropdown button {
    font-size: 1.1rem;
  }

  .navbar-dropdown-navigation {
    font-size: 0.8rem;
  }
  .home-navbar-navigations a {
    font-size: 1rem;
  }

  .home-page-bottom-tracker {
    min-width: unset;
    grid-template-columns: 220px 220px 220px;
    width: auto;
    bottom: 20px;
  }

  .home-page-bottom-tracker--card h3 {
    font-size: 0.9rem;
  }

  .home-page-map-navigations--card h3 {
    font-size: 1.1rem;
  }

  .home-page-map-navigations {
    width: 250px;
    left: 25px;
  }
}

.GettingStarted {
  position: relative;
}

.GettingStarted img {
  display: none;
}

