.farm_dashboard_container {
  width: 100vw;
  height: 100vh;
  display: flex;
}
.farm_dashboard_sidebar {
  width: 100px;
  height: 100%;
  border-radius: 20px;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* border: 10px solid white; */
  padding: 10px;
}
.farm_dashboard {
  width: 100%;
  height: 100%;
  padding: 25px;
}
.farm_dashboard_sidebar img {
  margin-top: 10px;
  max-width: 50px;
}
.sidebar_navigrations {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
